.News {
    box-sizing: border-box;
}

.Loader {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    -webkit-transform: translate(-50%, -50%);
    -moz-transform: translate(-50%, -50%);
    -o-transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
}

.News .NewsContent {
    padding: 1%;
}

.News .NewsContent .nushk_logo {
    top: 0;
    left: 0;
	color: #f2f2f2;
    text-decoration: none;
    text-align: left;
    background-color: rgb(26 39 76);
    height: 45px;
    position: fixed;
}

.News .NewsContent .nushk_logo .logo {
    padding: 0% 1% 0% 2%;
    width: 6%;
}

.News .NewsContent .NewsCard {
    display: flex;
    padding: 1%;
    margin: 4%;
    background-color: powderblue;
}

.News .NewsContent .NewsCard:first-child {
    top: 40px;
}

.News .NewsContent .NewsCard .urlToImage {
    width: 10%;
    padding: 1%;
    margin: 1%;
}

.News .NewsContent .NewsCard .news_content {
    padding: 1%;
    margin: 1%;
}

.News .NewsContent .NewsCard .news_content .title{
    padding: 1%;
    margin: 1%;
}

.News .NewsContent .NewsCard .news_content .description{
    padding: 1%;
    margin: 1%;
}

.News .NewsContent .Pagination {
    text-align: right;
}

.News .NewsContent .Pagination .pagination {
    display: inline-block;
    padding-left: 0;
    margin: 20px 0;
    border-radius: 4px;
}

.News .NewsContent .Pagination ul {
    display: block;
    list-style-type: disc;
    margin-block-start: 1em;
    margin-block-end: 1em;
    margin-inline-start: 0px;
    margin-inline-end: 0px;
    padding-inline-start: 40px;
}

.News .NewsContent .Pagination .pagination > li {
    display: inline;
}

.News .NewsContent .Pagination li {
    display: list-item;
    text-align: -webkit-match-parent;
}

.News .NewsContent .Pagination ul {
    list-style-type: disc;
}

.News .NewsContent .Pagination .pagination > li:first-child > a, .News .NewsContent .Pagination .pagination > li:first-child > span {
    margin-left: 0;
    border-bottom-left-radius: 4px;
    border-top-left-radius: 4px;
}

.News .NewsContent .Pagination .pagination > .disabled > span, .News .NewsContent .Pagination .pagination > .disabled > span:hover, .News .NewsContent .Pagination .pagination > .disabled > span:focus, .News .NewsContent .Pagination .pagination > .disabled > a, .pagination > .disabled > a:hover, .News .NewsContent .Pagination .pagination > .disabled > a:focus {
    color: #777777;
    background-color: #fff;
    border-color: #ddd;
    cursor: not-allowed;
}

.News .NewsContent .Pagination .pagination > li > a, .News .NewsContent .Pagination .pagination > li > span {
    position: relative;
    float: left;
    padding: 6px 12px;
    line-height: 1.42857143;
    text-decoration: none;
    color: #337ab7;
    background-color: #fff;
    border: 1px solid #ddd;
    margin-left: -1px;
}

.News .NewsContent .Pagination .pagination > .active > a, .News .NewsContent .Pagination .pagination > .active > span, .News .NewsContent .Pagination .pagination > .active > a:hover, .News .NewsContent .Pagination .pagination > .active > span:hover, .News .NewsContent .Pagination .pagination > .active > a:focus, .News .NewsContent .Pagination .pagination > .active > span:focus {
    z-index: 3;
    color: #fff;
    background-color: #337ab7;
    border-color: #337ab7;
    cursor: default;
}

.News .NewsContent .Pagination .pagination > li:last-child > a, .News .NewsContent .Pagination .pagination > li:last-child > span {
    border-bottom-right-radius: 4px;
    border-top-right-radius: 4px;
}