.Header {
    box-sizing: border-box;
    width: 100%;
    height: 45px;
    display: flex;
    background-color: rgb(26 39 76);
    color: #fff;
    position: fixed;
}

.Header .header_logo {
    padding: 0.5% 1%;
    margin: 0.5% 1%;
    margin-right: 10%;
    width: 50%;
    color: #f2f2f2;
    text-align: left;
    text-decoration: none;
}

.Header .header_content {
    padding: 0.5% 1%;
    margin: 0.5% 1%;
    color: #f2f2f2;
    text-align: center;
    text-decoration: none;
}

.Header .header_logo .logo {
    width: 10%;
}