.Navbar {
    box-sizing: border-box;
    width: 100%;
    height: 45px;
    display: flex;
    background-color: rgb(26 39 76); 
    color: #fff;
    position: fixed;
}

.Navbar .navbar_card {
    top: 0;
	text-align: center;
	padding: 0.75% 2.5% 0.5% 2.5%;
    color: #f2f2f2;
    text-align: center;
    text-decoration: none;
}

.Navbar .navbar_card:first-child {
    padding: 0% 1% 0% 1%;
    margin: 0 10% 0 1%;
    text-align: left;
}

.Navbar .navbar_card:first-child:hover {
    background-color: rgb(26 39 76);
}

/* .Navbar .navbar_card:first-child:active {
    background-color: rgb(26 39 76);
} */

.Navbar .navbar_card:hover {
	background-color: #ddd;
	color: black;
}

